#page-container {
  min-height: 100vh;
  width: 100%;
  margin: 0;
}

#content-wrap {
  width: 100%;
  height: 190vh;
  background: #13377F;
  margin: auto;
  padding-bottom: 4rem;    /* Footer height */
}

/* HEADER */
.header {
  z-index: 1;
  min-width: auto;
  height:100%;
  top: 0;
  left: 0;
  width:100%;
  height:80px;
  position: sticky;
  background: #F7FBFF; /*F7FBFF*/
}

.header-logo {
  background: url('../src/images/ShineShareLogoNoBack.svg');
  height: 50px;
  width: 27px;
  display: inline-block;
  background-repeat: no-repeat;
  margin-top: 15px; 
  margin-left: 15px;
  margin-right: 15px;
}

.text-logo {
  display: inline;
  position: sticky;
  font-family: 'Segoe UI';
}

.text-logo1{
  display: inline-block;
  color: #1F9AFF;
  font-family: 'Segoe UI';
  font-size: 50px;
  font-weight: bold;
}
.text-logo2{
  display: inline-block;
  color: #13377F;
  font-family: 'Segoe UI';
  font-size: 50px;
  font-weight: bold;
}

/* NavBar */

.nav-center{
  position: fixed;
  margin-left: -350px;
  margin-top: 95px;
}

.large-center {
  position: relative;
  border-radius: 50%;
  height: 700px;
  width: 700px;
  background: #1F9AFF;
  /* 
    Child elements with absolute positioning will be 
    positioned relative to this div 
   */
}
.small-center {
  position: absolute;
  border-radius: 50%;
  top: 50%;
  left: 50%;
  height: 400px;
  width: 400px;
  margin: -200px 0px 0px -200px;
  background: #F7FBFF;
}

.large-logo {
  position: absolute;
  background: url('../src/images/ShineShareLogoNoBack.svg');
  display: inline-block;
  background-repeat: no-repeat;
  height: 400px;
  width: 100px;
  top: 25%;
  left: 60%;
}

.navbar-item {
  position: fixed;
  z-index: 1;
  font-family: 'Montserrat';
  color: #F7FBFF;
  font-size: 50px;
  margin-right: 20px; 
}

.navItem0 {
  margin-top: 7.5%;
  
}

.navItem1 {
  margin-top: 14%;
  margin-left: 10%;
}

.navItem2 {
  margin-top: 22.5%;
  margin-left: 12%;
}

.navItem3 {
  margin-top: 30%;
  margin-left: 10%;
}

.navItem4 {
  margin-top: 36.5%;
}

.navSelected {
  background: #00CBFF;
  height: 65px;
}

/* POPULAR HUNTS */
#popular-hunts {
  position: absolute;
  background: #1F9AFF;
  width: 300px;
  right: 0px;
  border-radius: 30px;
  margin: 50px;
  padding: 15px;
}

.popular-hunts-card {
  margin: 5px;
}

.popular-hunts-card-text {
  position: relative;
  display: inline-block;
  width: 120px;
  height: 120px;
  margin: 0;
}

#pop-name {

}

#pop-game {

}

#pop-image {
  position: relative;
  display: inline;
  height: 100px;
}


/* NEWS */
#news {
  position: absolute;
  background: #1F9AFF;
  width: 650px;
  left: 750px;
  border-radius: 30px;
  margin-top: 50px;
  padding: 15px;
}

/*FOOTER*/
#footer {
  position: fixed;
  bottom: 0;
}
#copyright{
  color: #F7FBFF;
}

/* GENERIC */
a {
  text-decoration: none;
}

div {
  color: #F7FBFF;
}

h1 {
  margin: 0;
  color: #F7FBFF;
}

h2 {
  color: #F7FBFF;
}

h3 {
  color: #F7FBFF;
}

ul {
  margin: 0;
}